import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useState, useEffect} from 'react'

import "./Registration.css"

import APIClient from "../service/APIClient"

// import axios from  "axios"
import {useNavigate} from "react-router-dom"

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();

export default function SignUp({user, setUser}) {


  const [errors, setErrors] = useState({})
  const navigate = useNavigate()
  const [form, setForm] = useState({
    firstName: "",
    lastName: "",
    email: "",
    password: ""
  })



  useEffect(() => {
    // if user is already logged in,
    // redirect them to the home page
    if (user?.email) {
      navigate("/portal")
    }
  }, [user, navigate])
  
 




  const handleOnInputChange = (event) => {

    if (event.target.name === "email") {
      if (event.target.value.indexOf("@") === -1) {
        setErrors((e) => ({ ...e, email: "Please enter a valid email." }))
      } else {
        setErrors((e) => ({ ...e, email: null }))
      }
    }

    setForm((f) => ({ ...f, [event.target.name]: event.target.value }))
  }




  const handleSubmit = async(event) => {
    setErrors((e) => ({ ...e, form: null }))
    
    event.preventDefault();


    const {data, error} = await APIClient.signUpUser({email : form.email, password : form.password, firstName : form.firstName, lastName : form.lastName})

    console.log("Error?", error)
    console.log("Data?", data)



    if (error){
      setErrors( (e) => ({...e, form : error}))
    }

    if (data?.user){

      setUser(data.user)
      APIClient.setToken(data.token)
    }



  //   try{

  //     const res = await axios.post("http://localhost:3001/author/register", {
  //       firstName: form.firstName,
  //       lastName: form.lastName,
  //       email: form.email,
  //       password: form.password,
  //     })



  //     if (res?.data?.user) {
  //       setUser(res.data.user)

  //     } else {
  //       setErrors((e) => ({ ...e, form: "Something went wrong with registration" }))
  //     }
      
  //   //   const data = new FormData(event.currentTarget);
  //   //   console.log({
  //   //   email: data.get('email'),
  //   //   password: data.get('password'),
  //   //   firstName : data.get('firstName'),
  //   //   lastName : data.get('lastName')
  //   // });
  
  // }

  //   catch(err){

  //     console.log(err)
  //     const message = err?.response?.data?.error?.message
  //     setErrors((e) => ({ ...e, form: message ? String(message) : String(err) }))
  //   }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign up
          </Typography>
          <Box component="form" noValidate onSubmit={handleSubmit} sx={{ mt: 3 }}  onChange={handleOnInputChange} >
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <TextField
                  autoComplete="given-name"
                  name="firstName"
                  required
                  fullWidth
                  id="firstName"
                  label="First Name"
                  autoFocus
                  
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="lastName"
                  label="Last Name"
                  name="lastName"
                  autoComplete="family-name"
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label="Email Address"
                  name="email"
                  autoComplete="email"
                />
              </Grid>
              {errors.email && <span className="error">{errors.email}</span>}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label="Password"
                  type="password"
                  id="password"
                  autoComplete="new-password"
                />
              </Grid>
              <Grid item xs={12}>
                <FormControlLabel
                  control={<Checkbox value="allowExtraEmails" color="primary" />}
                  label="I want to receive inspiration, marketing promotions and updates via email."
                />
              </Grid>
            </Grid>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign Up
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link href="/login" variant="body2">
                  Already have an account? Sign in
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 5 }} />
      </Container>
    </ThemeProvider>
  );
}